import React from 'react';
import { NavLink } from 'react-router-dom';
import { useDispatch } from "services/hooks/hooks";
import { exitUserThunk } from "services/actions/user";
import useAuth from 'services/auth';

import profileMenuStyles from './profile-menu.module.css';

const ProfileMenu = () => {
  const dispatch = useDispatch();
  const { refreshToken } = useAuth();

  return (
    <section className={`${profileMenuStyles['profile-menu']}`}>
      <nav className={`${profileMenuStyles['profile-menu__navigation']}`}>
        <NavLink exact to="/profile" activeClassName={`${profileMenuStyles['profile-menu__link_active']}`} className={`${profileMenuStyles['profile-menu__link']} text text_type_main-medium text_color_inactive`} ></NavLink>
        <NavLink to="/profile/orders" activeClassName={`${profileMenuStyles['profile-menu__link_active']}`} className={`${profileMenuStyles['profile-menu__link']} text text_type_main-medium text_color_inactive`}> </NavLink>
        <button className={`${profileMenuStyles['profile-menu__link']} text text_type_main-medium text_color_inactive`} onClick={() => dispatch(exitUserThunk(refreshToken))}></button>
      </nav>
      <p className={`${profileMenuStyles['profile-menu__caption']} text text_type_main-default text_color_inactive pt-20`}>        </p>
    </section>
  )
};

export default ProfileMenu;